.container {
  align-items: center;
  background: rgba(55, 102, 173, 0.787);
  display: flex;
  flex-direction: column;
  font-size: 1.3rem;
  min-height: 100vh;
  padding: 1rem;
  width: 100%; }
  .container__title {
    font-size: 2rem;
    text-align: center;
    text-shadow: 0 0 4px black; }
  .container__glass {
    align-items: center;
    display: flex;
    flex-direction: column; }
  .container__add-glass {
    color: #bff5f7;
    background-color: #3767AD;
    border: 1px solid white;
    border-radius: 50px;
    height: 3rem;
    margin-top: 2rem;
    width: 13rem;
    outline: none;
    text-shadow: 0 0 4px black;
    box-shadow: 0 1px 2px 0 black; }
  .container__del-glass {
    background: none;
    border: none;
    color: #bff5f7;
    margin-top: 2rem;
    outline: none;
    text-shadow: 0 0 4px black; }

.glass {
  padding: 1rem;
  position: relative; }
  .glass__counter {
    color: #171718;
    font-size: 5rem;
    font-weight: 700;
    margin: 0;
    position: absolute;
    top: calc(50% - 2rem); }

html {
  box-sizing: border-box;
  font-size: 16px; }

*, *:before, *:after {
  box-sizing: inherit; }

body {
  color: #bff5f7;
  background-image: url("../assets/img/water1.jpg");
  background-repeat: repeat;
  background-size: cover;
  font-family: 'Roboto', sans-serif; }
